.advantages {
    padding: 20px 20.625%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    background-color: rgba(255, 255, 255, .8);
    // position: absolute;
    bottom: 0;
    z-index: 10;

    &__item {
        text-align: center;
        flex-basis: 260px;
        .P;
        color: @b;

        svg {
            margin-bottom: 12px;
        }
    }

    &__titel {
        font-weight: bold;
        font-size: 22px;
        margin-bottom: 10px;
    }

    &__description {
        font-size: 16px;
    }
}

@media only screen and (max-width: 1024px) {
    .advantages {
        padding: 20px 12.625%;
        &__item {
            padding: 20px 0;
        }
    }
}

@media only screen and (max-width: 414px) {
    .advantages {
            justify-content: center;
    }
}

  