.nav {
    background-color: rgba(0, 0, 0, .6);
    // position: absolute;
    top: 0;
    z-index: 10;
    width: 100%;
    
    ul {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        padding: 13px 0 12px;
        li {
            padding: 0 64px;
            list-style-type: none;
            border-right: 1px solid rgb(204, 204, 204);;

            a {
                color: @f;
                .P;
                font-size: 14px;
                word-spacing: 5px;
                text-transform: uppercase;
                cursor: pointer;

                &.active,
                &:hover {
                    font-weight: bold;
                }
            }
        }
    }
}

@media only screen and (max-width: 1024px) {
    .nav {
        ul {
            li {
                padding: 0 32px;
            }
        }
    }
}

