@f: #fff;
@b: #000;
@bl: #064180;

@font-face {
    font-family: "ProximaNova";
    src: url("/fonts/ProximaNova-Regular.woff2") format("woff2");
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: "ProximaNova";
    src: url("/fonts/ProximaNova-Bold.woff2") format("woff2");
    font-weight: bold;
    font-style: normal;
}

@font-face{
    font-family: "Intro";
    src: url("/fonts/Intro-Regular.woff2") format("woff2");
    font-weight: normal;
    font-style: normal;
}

@font-face{
    font-family: "Intro";
    src: url("/fonts/Intro-Black.woff2") format("woff2");
    font-weight: 900;
    font-style: normal;
}

@font-face{
    font-family: "Intro";
    src: url("/fonts/Intro-Black-Italic.woff2") format("woff2");
    font-weight: 900;
    font-style: italic;
}


.P {
    font-family: "ProximaNova";
}

.I {
    font-family: "Intro";
}

* {
    margin: 0;
    padding: 0;
    outline: none;
    word-break: break-word;
}

a {
    text-decoration: none;
}

.main {
    position: relative;
    overflow: hidden;
}

@media only screen and (max-width: 450px) {
    .heading {
        font-size: 48px;
    }
}


@import 'header';
@import 'nav';
@import 'content';
@import 'slider';
@import 'advantages';
