

@small_width: 100%; 	//Slider width - small display
@small_height: 55%; 	//Slider height - small display
@small_nav_width: 15%;// Previous and Next width - small display

.slider {
	// position: relative;
	position: absolute;
	width: 100%;
	height: calc(100vh - 56px);
	user-select: none;
	background-color: @bl;
	top: 0;
	z-index: -2;

	input {
		display: none;
	}

	.slide_img {
		position: absolute;
		width: 100%;
		height: 100%;
		z-index: -1;

		img {
			width: inherit;
			height: inherit;
		}

		
	}
}

#nav_slide {
	width: 231px;
	height: 11px;
	text-align: center;
	z-index: 99;
	cursor: default;

	.dots {
		top: 0;
		width: 15px;
		height: 15px;
		margin: 0 3px;
		position: relative;
		border-radius: 100%;
		display: inline-block;
		border: 2px solid @f;
		transition: .4s;
		cursor: pointer;
		background-clip: content-box;
		box-sizing: border-box;
		
		&:hover {
			background: @f;
			background-clip: content-box;
			padding: 2px;
		}
	}
}

.arrow {
	width: 231px;
	z-index: 99;
	position: relative;
	padding-bottom: 50px;
	
	.prev, .next {
		position: absolute;
		top: -23px;
		transition: .45s;
		cursor: pointer;
		text-align: center;
	}
	.next {
		right: 0;
	}
	.prev {
		left: 0;
	}

	.prev:hover, .next:hover {
		transition: .3s;
	}

	.prev img, .next img {
		width: 25px;
		height: 40px;
	}
}

#i1:checked ~ #one,
#i2:checked ~ #two,
#i3:checked ~ #three,
#i4:checked ~ #four {
	z-index: 9;
	animation: scroll 1s ease-in-out;
}

#i1:checked ~ #nav_slide #dot1 {
    background: @f;
    background-clip: content-box;
    padding: 2px;
}
#i2:checked ~ #nav_slide #dot2 {
    background: @f;
    background-clip: content-box;
    padding: 2px;
}
#i3:checked ~ #nav_slide #dot3 {
    background: @f;
    background-clip: content-box;
    padding: 2px;
}
#i4:checked ~ #nav_slide #dot4 {
    background: @f;
    background-clip: content-box;
    padding: 2px;
}

  

@keyframes scroll {
	0% { opacity: .4;}
	100% { opacity: 1;}
}

