.header {
    padding: 9.5px 20.313%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    .P;

    .logo {
        display: flex;
        align-items: center;
        color: @b;

        &__titel {
            font-size: 30px;
            letter-spacing: 5px;
            margin-right: 27px;
        }

        &__description {
            font-size: 13px;
            width: 198px;
            text-transform: uppercase;
        }
    }

    .callback {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        font-weight: bold;

        &__number {
            color: @b;
            font-size: 35px;
            line-height: 21px;
            margin-right: 16px;
        }

        &__button {
            font-size: 13px;
            color: @f;
            padding: 7px 10px;
            border-radius: 4px;
            background-image: linear-gradient( 90deg, rgb(255,79,88) 0%, rgb(253,112,72) 100%);
        }
    }
}

@media only screen and (max-width: 1024px) {
    .header {
        padding: 9.5px 5.313%;
    }
}

@media only screen and (max-width: 414px) {
    .header {
        .callback {
            justify-content: center;

            &__number {
                padding: 10px 0;
            }
        }
    }
}


  