.content {
    position: relative;
    // position: absolute;
    z-index: 10;
    left: 20%;
    padding-top: 35px;
    width: fit-content;

    &::before {
        content: '';
        position: absolute;
        background-color: rgba(0, 0, 0, .4);
        width: 815px;
        height: -webkit-fill-available;
        transform: skew(-20deg);
        z-index: -1;
        left: -80%;
        top: 0;
    }

    .wallpaper {
        position: relative;
        margin-bottom: 28px;
        &:before {
            content: '';
            position: absolute;
            left: -25px;
            top: 0;
            width: 100%;
            height: 100%;
            border-left: 4px solid rgb(255, 255, 255);
            z-index: -1;
            transform: skew(-20deg);
        }

        &__titel {
            .I;
            font-size: 90px;
            color: @b;
            text-transform: uppercase;
            padding: 0px 34px;
            font-weight: 900;
            position: relative;
            margin-bottom: 16px;

            &:before {
                content: '';
                position: absolute;
                left: 0;
                top: 0;
                width: 613px;
                height: 110px;
                background-color: rgba(255, 255, 255, 0.7);
                z-index: -1;
                transform: skew(-20deg)
            }
        }

        &__info {
            display: flex;
        }

        &__for {
            .I;
            font-style: italic;
            font-weight: 900;
            font-size: 36px;
            position: relative;
            padding: 0 65px;
            margin-left: -32px;
            margin-right: 15px;

            &:before {
                content: '';
                position: absolute;
                left: 0;
                top: -8px;
                width: 100%;
                height: 63px;
                background-color: rgba(255, 255, 255, 0.7);
                z-index: -1;
                transform: skew(-20deg)
            }

            &:after {
                content: '';
                position: absolute;
                left: 0;
                right: 0;
                bottom: 8px;
                margin: auto;
                width: 226px;
                height: 3px;
                background-color: @b;
            }
        }

        .price {
            .I;
            font-size: 16px;
            font-weight: 900;
            color: @f;
            position: relative;
            padding: 0 15px;

            span {
                font-size: 45px;
                padding: 0 14px;
            }

            &::before {
                content: '';
                position: absolute;
                left: 0;
                top: 0;
                width: 193px;
                height: 50px;
                background-color: rgba(255, 255, 255, 0.7);
                z-index: -1;
                transform: skew(-20deg);
                box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.75);
                background-image: linear-gradient( 0deg, rgb(42,235,42) 0%, rgb(0,117,0) 100%);
            }
        }
    }

    .ideas {
        width: 255px;
    }

    .above {
        .P;
        font: 19px;
        color: @f;
        font-weight: 900;
        text-transform: uppercase;
        margin-bottom: 24px;
    }

    .watch {
        .P;
        font-weight: 900;
        font-size: 23px;
        color: @f;
        background-image: linear-gradient( 90deg, rgb(255,79,88) 0%, rgb(253,112,72) 100%);
        border-radius: 2px;
        border: 2px solid @f;
        padding: 8px 18px;
        display: inline-block;
        text-transform: uppercase;
        margin-bottom: 60px;
    }
}

@media only screen and (max-width: 1024px) {
    .content {
        left: 12%;
    }
}

@media only screen and (max-width: 414px) {
    .content {
        .wallpaper {
            &__titel {
                font-size: 45px;
                padding: 0 15px;
                &::before {
                    width: 100%;
                    height: 100%;
                }
            }

            &__for {
                font-size: 18px;
                position: relative;
                padding: 0 32px;

                &::before {
                    height: 100%;
                    top: 0px;
                    left: 8px;
                }

                &:after {
                    width: 80%;
                }
            }
        }
    }
}
  