@font-face {
  font-family: "ProximaNova";
  src: url("/fonts/ProximaNova-Regular.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "ProximaNova";
  src: url("/fonts/ProximaNova-Bold.woff2") format("woff2");
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: "Intro";
  src: url("/fonts/Intro-Regular.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Intro";
  src: url("/fonts/Intro-Black.woff2") format("woff2");
  font-weight: 900;
  font-style: normal;
}

@font-face {
  font-family: "Intro";
  src: url("/fonts/Intro-Black-Italic.woff2") format("woff2");
  font-weight: 900;
  font-style: italic;
}

.P {
  font-family: "ProximaNova";
}

.I {
  font-family: "Intro";
}

* {
  margin: 0;
  padding: 0;
  outline: none;
  word-break: break-word;
}

a {
  text-decoration: none;
}

.main {
  position: relative;
  overflow: hidden;
}

.header {
  padding: 9.5px 20.313%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  font-family: "ProximaNova";
}

.header .logo {
  display: flex;
  align-items: center;
  color: #000;
}

.header .logo__titel {
  font-size: 30px;
  letter-spacing: 5px;
  margin-right: 27px;
}

.header .logo__description {
  font-size: 13px;
  width: 198px;
  text-transform: uppercase;
}

.header .callback {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  font-weight: bold;
}

.header .callback__number {
  color: #000;
  font-size: 35px;
  line-height: 21px;
  margin-right: 16px;
}

.header .callback__button {
  font-size: 13px;
  color: #fff;
  padding: 7px 10px;
  border-radius: 4px;
  background-image: linear-gradient(90deg, #ff4f58 0%, #fd7048 100%);
}

.nav {
  background-color: rgba(0, 0, 0, 0.6);
  top: 0;
  z-index: 10;
  width: 100%;
}

.nav ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 13px 0 12px;
}

.nav ul li {
  padding: 0 64px;
  list-style-type: none;
  border-right: 1px solid #cccccc;
}

.nav ul li a {
  color: #fff;
  font-family: "ProximaNova";
  font-size: 14px;
  word-spacing: 5px;
  text-transform: uppercase;
  cursor: pointer;
}

.nav ul li a.active,
.nav ul li a:hover {
  font-weight: bold;
}

.content {
  position: relative;
  z-index: 10;
  left: 20%;
  padding-top: 35px;
  width: fit-content;
}

.content::before {
  content: '';
  position: absolute;
  background-color: rgba(0, 0, 0, 0.4);
  width: 815px;
  height: -webkit-fill-available;
  transform: skew(-20deg);
  z-index: -1;
  left: -80%;
  top: 0;
}

.content .wallpaper {
  position: relative;
  margin-bottom: 28px;
}

.content .wallpaper:before {
  content: '';
  position: absolute;
  left: -25px;
  top: 0;
  width: 100%;
  height: 100%;
  border-left: 4px solid #ffffff;
  z-index: -1;
  transform: skew(-20deg);
}

.content .wallpaper__titel {
  font-family: "Intro";
  font-size: 90px;
  color: #000;
  text-transform: uppercase;
  padding: 0px 34px;
  font-weight: 900;
  position: relative;
  margin-bottom: 16px;
}

.content .wallpaper__titel:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 613px;
  height: 110px;
  background-color: rgba(255, 255, 255, 0.7);
  z-index: -1;
  transform: skew(-20deg);
}

.content .wallpaper__info {
  display: flex;
}

.content .wallpaper__for {
  font-family: "Intro";
  font-style: italic;
  font-weight: 900;
  font-size: 36px;
  position: relative;
  padding: 0 65px;
  margin-left: -32px;
  margin-right: 15px;
}

.content .wallpaper__for:before {
  content: '';
  position: absolute;
  left: 0;
  top: -8px;
  width: 100%;
  height: 63px;
  background-color: rgba(255, 255, 255, 0.7);
  z-index: -1;
  transform: skew(-20deg);
}

.content .wallpaper__for:after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  bottom: 8px;
  margin: auto;
  width: 226px;
  height: 3px;
  background-color: #000;
}

.content .wallpaper .price {
  font-family: "Intro";
  font-size: 16px;
  font-weight: 900;
  color: #fff;
  position: relative;
  padding: 0 15px;
}

.content .wallpaper .price span {
  font-size: 45px;
  padding: 0 14px;
}

.content .wallpaper .price::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 193px;
  height: 50px;
  background-color: rgba(255, 255, 255, 0.7);
  z-index: -1;
  transform: skew(-20deg);
  box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.75);
  background-image: linear-gradient(0deg, #2aeb2a 0%, #007500 100%);
}

.content .ideas {
  width: 255px;
}

.content .above {
  font-family: "ProximaNova";
  font: 19px;
  color: #fff;
  font-weight: 900;
  text-transform: uppercase;
  margin-bottom: 24px;
}

.content .watch {
  font-family: "ProximaNova";
  font-weight: 900;
  font-size: 23px;
  color: #fff;
  background-image: linear-gradient(90deg, #ff4f58 0%, #fd7048 100%);
  border-radius: 2px;
  border: 2px solid #fff;
  padding: 8px 18px;
  display: inline-block;
  text-transform: uppercase;
  margin-bottom: 60px;
}

.slider {
  position: absolute;
  width: 100%;
  height: calc(100vh - 56px);
  user-select: none;
  background-color: #064180;
  top: 0;
  z-index: -2;
}

.slider input {
  display: none;
}

.slider .slide_img {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.slider .slide_img img {
  width: inherit;
  height: inherit;
}

#nav_slide {
  width: 231px;
  height: 11px;
  text-align: center;
  z-index: 99;
  cursor: default;
}

#nav_slide .dots {
  top: 0;
  width: 15px;
  height: 15px;
  margin: 0 3px;
  position: relative;
  border-radius: 100%;
  display: inline-block;
  border: 2px solid #fff;
  transition: 0.4s;
  cursor: pointer;
  background-clip: content-box;
  box-sizing: border-box;
}

#nav_slide .dots:hover {
  background: #fff;
  background-clip: content-box;
  padding: 2px;
}

.arrow {
  width: 231px;
  z-index: 99;
  position: relative;
  padding-bottom: 50px;
}

.arrow .prev,
.arrow .next {
  position: absolute;
  top: -23px;
  transition: 0.45s;
  cursor: pointer;
  text-align: center;
}

.arrow .next {
  right: 0;
}

.arrow .prev {
  left: 0;
}

.arrow .prev:hover,
.arrow .next:hover {
  transition: 0.3s;
}

.arrow .prev img,
.arrow .next img {
  width: 25px;
  height: 40px;
}

#i1:checked ~ #one,
#i2:checked ~ #two,
#i3:checked ~ #three,
#i4:checked ~ #four {
  z-index: 9;
  animation: scroll 1s ease-in-out;
}

#i1:checked ~ #nav_slide #dot1 {
  background: #fff;
  background-clip: content-box;
  padding: 2px;
}

#i2:checked ~ #nav_slide #dot2 {
  background: #fff;
  background-clip: content-box;
  padding: 2px;
}

#i3:checked ~ #nav_slide #dot3 {
  background: #fff;
  background-clip: content-box;
  padding: 2px;
}

#i4:checked ~ #nav_slide #dot4 {
  background: #fff;
  background-clip: content-box;
  padding: 2px;
}

@keyframes scroll {
  0% {
    opacity: 0.4;
  }

  100% {
    opacity: 1;
  }
}

.advantages {
  padding: 20px 20.625%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  background-color: rgba(255, 255, 255, 0.8);
  bottom: 0;
  z-index: 10;
}

.advantages__item {
  text-align: center;
  flex-basis: 260px;
  font-family: "ProximaNova";
  color: #000;
}

.advantages__item svg {
  margin-bottom: 12px;
}

.advantages__titel {
  font-weight: bold;
  font-size: 22px;
  margin-bottom: 10px;
}

.advantages__description {
  font-size: 16px;
}

@media only screen and (max-width: 1024px) {
  .header {
    padding: 9.5px 5.313%;
  }

  .nav ul li {
    padding: 0 32px;
  }

  .content {
    left: 12%;
  }

  .slider {
    height: 52vh;
  }

  .advantages {
    padding: 20px 12.625%;
  }

  .advantages__item {
    padding: 20px 0;
  }
}

@media only screen and (max-width: 450px) {
  .heading {
    font-size: 48px;
  }
}

@media only screen and (max-width: 414px) {
  .header .callback {
    justify-content: center;
  }

  .header .callback__number {
    padding: 10px 0;
  }

  .content .wallpaper__titel {
    font-size: 45px;
    padding: 0 15px;
  }

  .content .wallpaper__titel::before {
    width: 100%;
    height: 100%;
  }

  .content .wallpaper__for {
    font-size: 18px;
    position: relative;
    padding: 0 32px;
  }

  .content .wallpaper__for::before {
    height: 100%;
    top: 0px;
    left: 8px;
  }

  .content .wallpaper__for:after {
    width: 80%;
  }

  .slider {
    height: 75vh;
  }

  .advantages {
    justify-content: center;
  }
}